import React, { useState } from "react";
import moment from "moment";
import { useDispatch } from "react-redux";
import * as types from "erp/account/ActionType/ActionType";
import { TextField, Button } from "@material-ui/core";

const DetailTrialBalanceMenu = () => {
  const dispatch = useDispatch();
  const [fromDate, setFromdate] = useState();
  const [toDate, setTodate] = useState();

  const onChange = e => {
    if (e.target.name === "fromDate") setFromdate(e.target.value);
    else if (e.target.name === "toDate") setTodate(e.target.value);
  };

  const searchData = () => {
    dispatch({
      type: types.SEARCH_DETAILTRIAL_REQUEST,
      params: {
        fromDate: moment(fromDate).format("yyyy-MM-DD"),
        toDate: moment(toDate).format("yyyy-MM-DD"),
      },
    });
  };

  return (
    <>
      <fieldset>
        <legend> [ 검색조건 ] </legend>
        <TextField name="fromDate" type={"date"} onChange={onChange} />
        <TextField name="toDate" type={"date"} onChange={onChange} />
        <Button variant={"outlined"} color={"primary"} onClick={searchData}>
          조회
        </Button>
      </fieldset>
    </>
  );
};

export default DetailTrialBalanceMenu;
