import React, { useState } from "react";

import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-material.css";
import Axios from "axios";
import { AgGridReact } from "ag-grid-react/lib/agGridReact";
import RegistHead from "./RegistHead";
import { Button, makeStyles } from "@material-ui/core";
import useInput from "util/useInput";
import DetailDialog from "./DetailDialog";

const Regist = () => {
  const [bomData, setBomData] = useState(null);
  const [itemCode, setItemCode] = useState(null);
  const [positionGridApi, setPositionGridApi] = useState();
  const [nodeId, setNodeId] = useState("");
  const [data, setData] = useState(null);

  const useStyles = makeStyles(theme => ({
    root: {
      "& > *": {
        margin: theme.spacing(1),
      },
      paddingLeft: 9,
    },
    fieldset: {
      width: 270,
    },
    button2: {
      paddingLeft: 10,
    },
    tField: {
      paddingLeft: 9,
      paddingTop: 60,
    },
    button1: {
      paddingLeft: 18,
    },
  }));
  const classes = useStyles();
  const getBomDeploy = async () =>
    await Axios.get("http://localhost:8282/logi/purchase/searchBomInfo.do", {
      params: {
        parentItemCode: itemCode,
      },
    })
      .then(response => {
        setBomData(response.data);
      })
      .catch(e => {
        console.log("조회하다 발생한 에러 >> ", e);
      });

  const checkRenderer = element => {
    if (element.data.no === 1) return null;
    else return '<input type="checkbox"/>';
  };

  //========================== 그리드 객체 준비 ==========================
  const onGridReady = params => {
    setPositionGridApi(params.api);
  };

  //========================== 그리드내용 ==========================
  const columnDefs = [
    { headerName: "", width: 35, valueGetter: "node.rowIndex + 1" },
    { headerName: "삭제", width: 60, cellRenderer: checkRenderer },
    { headerName: "출력순서지정", field: "no", width: 100 },
    { headerName: "품목구분", field: "itemClassificationName", width: 80 },
    { headerName: "품목코드", field: "itemCode", width: 100 },
    { headerName: "품목명", field: "itemName", width: 130 },
    { headerName: "정미수량", field: "netAmount", width: 80 },
    { headerName: "설명", field: "description", width: 60 },
    {
      headerName: "status",
      field: "status",
      width: 80,
      valueFormatter: "value.toUpperCase()",
    },
    {
      headerName: "itemClassification",
      field: "itemClassification",
      width: 50,
      hide: true,
    },
    {
      headerName: "parentItemCode",
      field: "parentItemCode",
      width: 50,
      hide: true,
    },
    {
      headerName: "beforeStatus",
      field: "beforeStatus",
      width: 50,
      hide: true,
    },
    {
      headerName: "deleteStatus",
      field: "deleteStatus",
      width: 50,
      hide: true,
    },
  ];

  const searchBom = () => {
    if (itemCode === "" || itemCode === undefined) {
      alert("품목코드검색을 먼저 해주세요");
      return;
    }
    getBomDeploy();
  };

  const addNewRow = () => {
    //console.log(positionGridApi.gridOptionsWrapper.gridOptions.rowData);
    const newItem = NewRowData(); //새로운 row를 변수에담음
    positionGridApi.updateRowData({ add: [newItem] }); // ag그리드 api로 그리드에 add 함
  };
  //추가할 컬럼 선언
  const NewRowData = () => {
    let newNo = "";
    const rows = positionGridApi.forEachNode(n => {
      newNo = n.data.no + 1;
    });
    let newData = {
      no: newNo,
      itemClassificationName: "",
      itemCode: "",
      itemName: "",
      netAmount: "",
      customerBusinessItems: "",
      description: "",
      status: "INSERT",
      deleteStatus: "LOCAL 삭제",
      parentItemCode: itemCode,
    };
    return newData;
  };

  const [detailDialogOpen, setDetailDialogOpen] = useState(false);
  const getItemCode = useInput("");
  let dCode = "";
  const getDetailCode = async () =>
    await Axios.get("http://localhost:8282/logi/base/codeList.do", {
      params: {
        divisionCode: dCode,
      },
    })
      .then(response => {
        setData(response.data.detailCodeList);
        setDetailDialogOpen(true);
      })
      .catch(e => {
        console.log("조회하다 발생한 에러 >> ", e);
      });

  const onCellClicked = node => {
    const rowIndex = node.rowIndex;

    if (node.data.no === "1") return;

    if (node.colDef.headerName === "삭제") {
      let idNo = node.data.no;
      let rowNode = positionGridApi.getRowNode(idNo);
      let newStatus = "";
      if (node.data.status === "DELETE") {
        newStatus = node.data.beforeStatus;
      } else {
        rowNode.setDataValue("beforeStatus", node.data.status);
        newStatus = "DELETE";
      }
      //console.log(newStatus);
      rowNode.setDataValue("status", newStatus);
    } else {
      setNodeId(node.data.no);
      if (
        node.colDef.field === "status" ||
        node.colDef.field === "description"
      ) {
        return;
      }
      if (node.colDef.field === "netAmount") {
        if (node.data.itemClassificationName === "") {
          alert("품목구분을 먼저 선택해주세요");
          return;
        }
        if (node.data.itemCode === "" || node.data.itemName === "") {
          alert("품목코드를 먼저 선택해주세요");
          return;
        }
        positionGridApi.getRowNode(node.data.no).setDataValue("netAmount", 1);
        return;
      }
      if (node.colDef.field === "itemClassificationName") {
        dCode = "IT";
      } else if (
        node.colDef.field === "itemCode" ||
        node.colDef.field === "itemName"
      ) {
        dCode = getItemCode.value;
      }
      getDetailCode();
    }
  };
  let resultList = [];

  const batchBom = async () =>
    await Axios.post(
      "http://localhost:8282/logi/purchase/batchBomListProcess.do",
      {
        batchList: JSON.stringify(resultList),
      },
    )
      .then(response => {
        setBomData(null);
      })
      .catch(e => {
        console.log("조회하다 발생한 에러 >> ", e);
      });

  const batchAll = () => {
    let insertCount = 0;
    let updateCount = 0;
    let deleteCount = 0;

    positionGridApi.forEachNode((node, index) => {
      let rowObject = node.data;
      let status = rowObject.status;
      if (status == "INSERT") {
        if (rowObject.itemClassification === "") {
          alert(
            "품목분류를 입력하지 않은 행이 있습니다 </br> 저장 목록에서 제외합니다",
          );
        } else if (rowObject.itemCode === "") {
          alert(
            "품목코드를 입력하지 않은 행이 있습니다 </br> 저장 목록에서 제외합니다",
          );
        } else if (rowObject.netAmount === "") {
          alert(
            "정미수량을 입력하지 않은 행이 있습니다 </br> 저장 목록에서 제외합니다",
          );
        } else {
          resultList.push(rowObject);
          insertCount++;
        }
      } else if (status === "UPDATE") {
        resultList.push(rowObject);
        updateCount++;
      } else if (status === "DELETE") {
        if (rowObject.deleteStatus !== "LOCAL 삭제") {
          resultList.push(rowObject);
          deleteCount++;
        } else {
          let removedRows = [];
          let selectedRow = node.data;
          removedRows.push(selectedRow);
          positionGridApi.updateRowData({ remove: [selectedRow] });
        }
      }
      //console.log(resultList);
    });
    let confirmMsg =
      (insertCount != 0 ? insertCount + "개의 항목 추가 \n" : "") +
      (updateCount != 0 ? updateCount + "개의 항목 수정 \n" : "") +
      (deleteCount != 0 ? deleteCount + "개의 항목 삭제 \n" : "") +
      "\r위와 같이 작업합니다. 계속하시겠습니까?";

    let confirmStatus = "";

    if (resultList.length !== 0) {
      confirmStatus = window.confirm(confirmMsg);
    }
    if (resultList.length !== 0 && confirmStatus) {
      batchBom();
    } else if (resultList.length !== 0 && !confirmStatus) {
      alert("취소되었습니다");
    } else if (resultList.length === 0) {
      alert("추가/수정/삭제할 항목이 없습니다");
    }

    resultList = []; // 초기화
  };

  const handleClose = value => {
    setDetailDialogOpen(false);
    if (value.data === undefined) return;
    if (value.data[0].detailCode.indexOf("IT") >= 0) {
      positionGridApi
        .getRowNode(nodeId)
        .setDataValue("itemClassificationName", value.data[0].detailCodeName);
    } else {
      let flag = false;
      positionGridApi.forEachNode((node, index) => {
        if (node.data.itemCode == value.data[0].detailCode) {
          if (node.data.no == "1") {
            alert("BOM 구성에 자기 자신인 품목을 등록할 수 없습니다");
          } else {
            alert("BOM 구성에 이미 있는 품목입니다");
          }
          flag = true;
        }
      });
      if (flag) return;
      positionGridApi
        .getRowNode(nodeId)
        .setDataValue("itemCode", value.data[0].detailCode);
      positionGridApi
        .getRowNode(nodeId)
        .setDataValue("itemName", value.data[0].detailCodeName);
    }
    if (positionGridApi.getRowNode(nodeId).data.status !== "INSERT")
      positionGridApi.getRowNode(nodeId).setDataValue("status", "UPDATE");
    getItemCode.setValue(value.data[0].detailCode);
  };

  return (
    <>
      <div>
        <RegistHead
          trItemCode={setItemCode}
          addNewRow={addNewRow}
          batchAll={batchAll}
        />
      </div>
      <div className={classes.button1}>
        <Button
          variant="contained"
          color="secondary"
          onClick={() => searchBom()}
          value="searchBom"
        >
          BOM조회
        </Button>
      </div>
      <div
        className={"ag-theme-balham-dark"} //그리드 모양
        style={{
          height: "500px",
          width: "100%",
          paddingTop: "8px",
        }}
      >
        <AgGridReact
          columnDefs={columnDefs} //컬럼명
          rowData={bomData} // 뿌릴 data
          onGridReady={onGridReady}
          onCellClicked={onCellClicked}
          getRowNodeId={data => {
            return data.no;
          }}
        />
      </div>
      <DetailDialog data={data} open={detailDialogOpen} close={handleClose} />
    </>
  );
};

export default Regist;
