import React, { useState } from "react";
import { TextField, Button, Grid } from "@material-ui/core";
import { useDispatch } from "react-redux";
import * as types from "erp/account/ActionType/ActionType";
import moment from "moment";

const JournalFormMenu = () => {
  const dispatch = useDispatch();
  const [startDate, setStartDate] = useState();
  const [endDate, setEndDate] = useState();

  const onChange = e => {
    if (e.target.name === "startDate") setStartDate(e.target.value);
    else if (e.target.name === "endDate") setEndDate(e.target.value);
  };

  const searchJournal = () => {
    dispatch({
      type: types.SEARCH_JOURNAL_FROM_REQUEST,
      params: {
        startDate: moment(startDate).format("yyyy-MM-DD"),
        endDate: moment(endDate).format("yyyy-MM-DD"),
      },
    });
  };

  return (
    <React.Fragment>
      <fieldset>
        <legend> [ 검색조건 ] </legend>
        <Grid
          container
          direction="row"
          justify="space-evenly"
          alignItems="center"
        >
          <Grid>
            <TextField name="startDate" type={"date"} onChange={onChange} />
          </Grid>
          <Grid>
            <TextField name="endDate" type={"date"} onChange={onChange} />
          </Grid>
          <Grid>
            <Button
              variant={"outlined"}
              color={"primary"}
              onClick={searchJournal}
            >
              조회
            </Button>
          </Grid>
        </Grid>
      </fieldset>
    </React.Fragment>
  );
};

export default JournalFormMenu;
