import React, { useState } from 'react';

import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-material.css";
import Axios from 'axios';
import { AgGridReact } from 'ag-grid-react/lib/agGridReact';
import DeployHead from './DeployHead';
import { Button } from '@material-ui/core';

const Deploy = () => {
    const [bomData, setBomData] = useState(null);  
    const [itemCode, setItemCode] = useState(null);  
    const [divisionCode, setDivisionCode] = useState(null);  
    const [deployCondition, setDeployCondition] = useState(null);  
    const [positionGridApi, setPositionGridApi] = useState();

    const getBomDeploy = async () =>
        await Axios.get(
            'http://localhost:8282/logi/purchase/searchBomDeploy.do', {
                params : {
                    deployCondition: deployCondition,
                    itemCode: itemCode,
                    itemClassificationCondition: divisionCode,
                }
            }
        )
        .then(response => {
            setBomData(response.data);
        })
        .catch(e => {
            console.log("조회하다 발생한 에러 >> ", e);
        });
    
    const setPadding = (arr) => {
        let lev = arr.data.bomLevel;
        let iCode = arr.value;
        let space = "........";
        for(let i=0; i<lev; i++)
            iCode = space+space+iCode;
        console.log(iCode);
        return iCode;
    };

    //========================== 그리드 객체 준비 ==========================
    const onGridReady = params => {
        setPositionGridApi(params.api);
    };
    //========================== 그리드내용 ==========================
    const columnDefs = [
        { headerName: "BOM번호", field: "bomNo", width: 100 },
        { headerName: "BOM레벨", field: "bomLevel", width: 80 },
        { headerName: "상위품목코드", field: "parentItemCode", width: 100 },
        { headerName: "품목코드", field: "itemCode", width: 200, valueFormatter: setPadding},
        { headerName: "품목명", field: "itemName", width: 130 },
        { headerName: "단위", field: "unitOfStock", width: 50 },
        { headerName: "정미수량", field: "netAmount", width: 80 },
        { headerName: "손실율", field: "lossRate", width: 65 },
        { headerName: "필요수량", field: "necessaryAmount", width: 80 },
        { headerName: "리드타임", field: "leadTime", width: 80 },
        { headerName: "isLeaf", field: "isLeaf", width: 70 },
        { headerName: "비고", field: "description", width: 50 },
    ];

    const searchBom = () => {
        if(deployCondition==''||deployCondition==undefined){
            alert("검색조건을 선택해주세요");
            return;
        }else if(itemCode==''||itemCode==undefined){
            alert("품목코드검색을 먼저 해주세요");
            return;
        }
        getBomDeploy();
    }

    return (
        <>
            <div>
                <DeployHead trCondition={setDeployCondition} 
                    trItemCode={setItemCode} 
                    trDivisionCode={setDivisionCode}
                    searchBom={searchBom}
                />
                
            </div>
            <div
                className={"ag-theme-balham-dark"} //그리드 모양
                style={{
                    height: "500px",
                    width: "100%",
                    paddingTop: "8px",
                }  }
            >
                <AgGridReact
                    columnDefs={columnDefs} //컬럼명
                    rowData={bomData} // 뿌릴 data
                    onGridReady={onGridReady}
                />
            </div>
        </>
    );
};

export default Deploy;