import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { Button, TextField } from "@material-ui/core";
import * as types from "erp/account/ActionType/ActionType";

const CashJournalMenu = () => {
  const dispatch = useDispatch();
  const [fromDate, setFromdate] = useState();
  const [toDate, setTodate] = useState();

  const onChange = e => {
    if (e.target.name === "fromDate") setFromdate(e.target.value);
    else if (e.target.name === "toDate") setTodate(e.target.value);
  };

  const searchData = () => {
    dispatch({
      type: types.SEARCH_CASHJOURNAL_REQUEST,
      params: {
        fromDate: fromDate,
        toDate: toDate,
      },
    });
  };

  return (
    <>
      <fieldset>
        <legend> [ 검색조건 ] </legend>
        <TextField
          name="fromDate"
          type={"date"}
          defaultValue={fromDate} //defaultValue : 초기값.
          onChange={onChange} // onChange : 값이 변경되면 콜백이 발생.
        />
        <TextField
          name="toDate"
          type={"date"}
          defaultValue={toDate}
          onChange={onChange}
        />
        <Button variant={"outlined"} color={"primary"} onClick={searchData}>
          조회
        </Button>
      </fieldset>
    </>
  );
};

export default CashJournalMenu;
