import React, { useState } from 'react';

import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-material.css";
import useInput from "util/useInput";
import {makeStyles,
        TextField,
        FormControlLabel,
        Radio,
        RadioGroup,
        Button,
} from "@material-ui/core";
import SearchItemCodeDialog from './SearchItemCodeDialog';
import Axios from 'axios';


const RegistHead = ({ trItemCode, addNewRow, batchAll }) => {

    const [openItemCodeDialog, setOpenItemCodeDialog] = useState(false); //다이알로그 기본값 false 하면 안보임
    const [divisionCode, setDivisionCode] = useState(''); //다이알로그 기본값 false 하면 안보임
    const [data, setData] = useState(null);  
    
    const getItemCode = useInput(""); //util에 있음 공통함수라고생각하면됨 ( 가져다쓰는거임 )
    
    const useStyles = makeStyles(theme => ({
        root: {
            '& > *': {
                margin: theme.spacing(2),
            },
        },
        fieldset: {
            width: 300,
            paddingTop: 20,
        },
        button2: {
            paddingLeft: 30
        },
        tField : {
            paddingLeft: 20,
            paddingTop: 35,
        }
    }));

    const classes = useStyles();
    const onDivisionChange = (e) =>{
        setDivisionCode(e.currentTarget.value);
    };

    //검색버튼을 누르면 다이알로그가 열린다
    const handleClickOpen = () => {
        if(divisionCode==''||divisionCode==undefined){
            alert("품목분류를 먼저 선택해주세요");
            return;
        }
        getDetailCode();
        setOpenItemCodeDialog(true);
    };

    const handleClose = value => {
        setOpenItemCodeDialog(false);
        if (value.data === undefined) return;
        getItemCode.setValue(value.data[0].detailCode);
        trItemCode(value.data[0].detailCode);
      };

    const getDetailCode = async () =>
        await Axios.get(
            'http://localhost:8282/logi/base/codeList.do', {
                params : {
                    divisionCode: divisionCode,
                }
            }
        )
        .then(response => {

            setData(response.data.detailCodeList);
        })
        .catch(e => {
            console.log("조회하다 발생한 에러 >> ", e);
        });
    

    return (
        <>
            <tr className={classes.root}>
                <td>
                    <RadioGroup
                        row
                        aria-label="itemCode"
                        onChange={onDivisionChange}
                    >
                        <fieldset>
                            <legend>
                                <strong>품목분류</strong>
                            </legend>
                            <FormControlLabel
                                value="IT-CI"
                                control={<Radio />}
                                label="완제품"
                            />
                            <FormControlLabel
                                value="IT-SI"
                                control={<Radio />}
                                label="반제품"
                            />
                        </fieldset>
                    </RadioGroup>
                </td>
                <td>
                    <TextField
                        className={classes.tField}
                        id="itemCode"
                        InputProps={{
                            endAdornment: (
                                <Button
                                    variant="contained"
                                    color="secondary"
                                    size="medium"
                                    value="searchCode"
                                    onClick={() => handleClickOpen()}
                                >검색</Button>
                            ),
                        }}
                        margin="normal"
                        placeholder="품목코드검색"
                        disabled={true}
                        value={getItemCode.value}
                    />
                </td>
                <td className={classes.button2}>
                    <fieldset className={classes.fieldset}>
                        <legend>
                            <strong>BOM 수정/삭제/저장</strong>
                        </legend>
                        <span>
                            <Button
                                variant="contained"
                                color="secondary"
                                size="medium"
                                value="addNew"
                                onClick={() => addNewRow()}
                            >새로운 항목 추가</Button>
                        </span>
                        <span className={classes.button2}>
                            <Button
                                variant="contained"
                                color="secondary"
                                size="medium"
                                onClick={() => batchAll()}
                                value="batchAll"
                            >일괄저장</Button>
                        </span>
                    </fieldset>
                </td>
                <SearchItemCodeDialog data={data} open={openItemCodeDialog} close={handleClose} />
            </tr>
        </>
    );
};


export default RegistHead;