import React, { useState } from "react";
import { TextField, Button } from "@material-ui/core";
import FindInPageIcon from "@material-ui/icons/FindInPage";
import { useDispatch } from "react-redux";
import * as types from "erp/account/ActionType/ActionType";

const IncomeStatementSearch = () => {
  const dispatch = useDispatch();
  const [approvalDate, setApprovalDate] = useState();

  const onChange = e => {
    setApprovalDate(e.target.value);
  };

  const searchIncome = () => {
    dispatch({
      type: types.SEARCH_INCOME_REQUEST,
      params: { date: approvalDate },
    });
  };

  return (
    <React.Fragment>
      <div Align="center">
        <fieldset>
          <legend> [ 검색조건 ] </legend>
          <TextField name="approvalDate" type={"date"} onChange={onChange} />
          <Button
            size="large"
            variant="contained"
            color="primary"
            disableElevation
            startIcon={<FindInPageIcon />}
            onClick={searchIncome}
          >
            조회
          </Button>
        </fieldset>
      </div>
    </React.Fragment>
  );
};

export default IncomeStatementSearch;
