import React from "react";
import { useSelector } from "react-redux";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";

const IncomeStatementGrid = () => {
  const data = useSelector(({ AccReducer }) => AccReducer.IncomeList, []);
  //const { error } = useSelector(state => state.AccReducer);
  const { isLoading } = useSelector(state => state.AccReducer);

  const IncomeStatementGrid = [
    {
      headerName: "번호",
      field: "accountInnerCode",
      hide: true,
    },
    {
      headerName: "계정명",

      field: "accountName",
      sortable: true, //컬럼눌러서 정렬가능하게하기
      cellClass: "grid-cell-centered",
    },
    {
      headerName: "당기 합계금액",
      field: "incomeSummary",
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" 원"',
    },
    {
      headerName: "전기 합계금액",
      field: "earlyIncomeSummary",
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" 원"',
    },
  ];

  return (
    <>
      <div
        className={"ag-theme-material"}
        skipHeaderOnAutoSize="true"
        enableColResize="true"
        enableSorting="true"
        enableFilter="true"
        enableRangeSelection="true"
        rowStyle={{ "text-align": "center" }}
        style={{
          height: "70%",
          width: "100%",
          paddingTop: "25px",
          float: "center",
        }}
        cellStyle={{ textAlign: "center" }}
      >
        {!isLoading ? (
          <AgGridReact
            columnDefs={IncomeStatementGrid}
            rowData={data}
            rowSelection="single"
            getRowStyle={function(param) {
              //가운데
              if (param.node.rowPinned) {
                return { "font-weight": "bold", background: "#dddddd" };
              }
              return { "text-align": "center" };
            }}
            onGridReady={event => {
              event.api.sizeColumnsToFit();
            }}
            // onGridReady={onGridReady}
            // onCellClicked={onCellClicked}
          />
        ) : (
          <h1 align="center">로딩중</h1>
        )}
      </div>
    </>
  );
};

export default IncomeStatementGrid;
