import React, { useEffect } from "react";
import { AgGridReact } from "ag-grid-react";
import { useSelector } from "react-redux";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";

const FinancialStatementsGrid = () => {
  const { data } = useSelector(state => state.AccReducer);
  const { error } = useSelector(state => state.AccReducer);
  const { isLoading } = useSelector(state => state.AccReducer);

  useEffect(() => {
    console.log(error);
    //데이터를 그리드에 세팅하는 함수
    // setGrid(datas);
  }, [error]);

  // 칼럼정의
  const FinancialStatementscolumnDefs = [
    {
      headerName: "당기 세부금액",
      field: "balanceDetail",
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" 원"',
    },
    {
      headerName: "당기 합계금액",
      field: "balanceSummary",
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" 원"',
    },
    { headerName: "계정과목", field: "accountName" },
    {
      headerName: "전기 세부금액",
      field: "preBalanceDetail",
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" 원"',
    },
    {
      headerName: "전기 합계금액",
      field: "preBalanceSummary",
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" 원"',
    },
  ];

  return (
    <>
      <div
        className={"ag-theme-balham-dark"}
        rowStyle={{ "text-align": "center" }}
        style={{
          height: "70%",
          width: "100%",
          paddingTop: "25px",
          float: "center",
        }}
        cellStyle={{ textAlign: "center" }}
      >
        {!isLoading ? (
          <AgGridReact
            columnDefs={FinancialStatementscolumnDefs}
            rowData={data}
            rowSelection="single"
            onGridReady={e => {
              e.api.sizeColumnsToFit();
            }}
            getRowStyle={function(param) {
              return { "text-align": "center" };
            }}
            // onGridReady={onGridReady}
            // onCellClicked={onCellClicked}
          />
        ) : (
          <h1 align="center">로딩중</h1>
        )}
      </div>
    </>
  );
};

export default FinancialStatementsGrid;
