import React, { useState } from "react";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";

import { Button } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import axios from "axios";

const TotalTrialBalanceGrid = ({ date }) => {
  // prop 값을 사용하기 편하게 비구조 할당
  const { approvalDate } = date;

  const [data, setData] = useState("");

  //===========================2020-09-18 조편백 수정  =======================================
  function selectData() {
    if (approvalDate.length === 0) {
      alert("조회할날짜를 선택해주세요.");
      return;
    } else {
      axios.get(
        'http://localhost:8282/acc/statement/getTotalTrialBalance',
        { params: { toDate: approvalDate, } }
      ).then(response => {
        console.log("????????? ", response.data.RESULT);
        setData(response.data.RESULT);
      }).catch(e => { console.log(e); });
    }
  }
  //========================================================================================


  const currencyFormatter = params => {
    return formatNumber(params.value) + " 원";
  };

  const formatNumber = number => {
    return Math.floor(number)
      .toString()
      .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
  };

  const TTBcolumnDefs = [
    { headerName: "차변 잔액", field: "debitsSumBalance", valueFormatter: currencyFormatter, },
    { headerName: "차변 합계", field: "debitsSum", valueFormatter: currencyFormatter, },
    { headerName: " 계정 과목 ", field: "accountName" },
    { headerName: "대변 합계", field: "creditsSum", valueFormatter: currencyFormatter, },
    { headerName: "대변 잔액", field: "creditsSumBalance", valueFormatter: currencyFormatter, },
  ];


  return (
    <React.Fragment>
      <br />
      <div align="right">
        <Button
          variant={"contained"}
          color={"primary"}
          onClick={selectData}
          startIcon={<SearchIcon />}
        >
          {" "}
          검색
        </Button>
      </div>
      <div
        className={"ag-theme-material"}
        rowStyle={{ "text-align": "center" }}
        style={{
          height: "700px",
          width: "100%",
          paddingTop: "20px",
          float: "center",
        }}
      >
        <AgGridReact
          columnDefs={TTBcolumnDefs}
          rowData={data}
          rowSelection="single"
          getRowStyle={function (param) { return { "text-align": "center" }; }} //body 가운데 정렬
          onGridReady={(e) => { e.api.sizeColumnsToFit(); }}
        // onCellClicked={onCellClicked}
        />
      </div>
    </React.Fragment>
  );
};

export default TotalTrialBalanceGrid;
