import React, { useState } from 'react';

import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-material.css";
import useInput from "util/useInput";
import {makeStyles,
        TextField,
        FormControlLabel,
        Radio,
        RadioGroup,
        Button,
} from "@material-ui/core";
import SearchItemCodeDialog from './SearchItemCodeDialog';
import Axios from 'axios';


const DeployHead = ({ trCondition, trItemCode, trDivisionCode, searchBom}) => {

    const [openItemCodeDialog, setOpenItemCodeDialog] = useState(false); //다이알로그 기본값 false 하면 안보임
    const [divisionCode, setDivisionCode] = useState(''); //다이알로그 기본값 false 하면 안보임
    const [data, setData] = useState(null);  
    
    const getItemCode = useInput(""); //util에 있음 공통함수라고생각하면됨 ( 가져다쓰는거임 )
    const useStyles = makeStyles(theme => ({
        formControl: {
            margin: theme.spacing(1),
            minWidth: 120
        },
        root: {
            '& > *': {
                margin: theme.spacing(2),
            },
        },
        tField : {
            paddingLeft: 10,
            paddingRight: 10,
            paddingTop: 35,
            paddingBottom: 15
        }
    }));

    const onDivisionChange = (e) =>{
        setDivisionCode(e.currentTarget.value);
        trDivisionCode(e.currentTarget.value);
    };
    const onConditionChange = (e) =>{
        trCondition(e.currentTarget.value);
    }

    //검색버튼을 누르면 다이알로그가 열린다
    const handleClickOpen = () => {
        if(divisionCode==''||divisionCode==undefined){
            alert("품목분류를 먼저 선택해주세요");
            return;
        }
        getDetailCode();
        setOpenItemCodeDialog(true);
    };
    const handleClose = value => {
        setOpenItemCodeDialog(false);
        if (value.data === undefined) return;
        getItemCode.setValue(value.data[0].detailCode);
        trItemCode(value.data[0].detailCode);
      };
    const classes = useStyles();
    
    const getDetailCode = async () =>
        await Axios.get(
            'http://localhost:8282/logi/base/codeList.do', {
                params : {
                    divisionCode: divisionCode,
                }
            }
        )
        .then(response => {

            setData(response.data.detailCodeList);
        })
        .catch(e => {
            console.log("조회하다 발생한 에러 >> ", e);
        });
    

    return (
        <>
            <div className={classes.root}>
                <tr>
                    <td>
                        <RadioGroup
                            row
                            aria-label="itemCode"
                            onChange={onDivisionChange}
                        >
                            <fieldset className={classes.text}>
                                <legend>
                                    <strong>품목분류</strong>
                                </legend>
                        
                                <FormControlLabel
                                    value="IT-CI"
                                    control={<Radio />}
                                    label="완제품"
                                />
                                <FormControlLabel
                                    value="IT-SI"
                                    control={<Radio />}
                                    label="반제품"
                                />
                                <FormControlLabel
                                    value="IT-MA"
                                    control={<Radio />}
                                    label="원재료"
                                />
                            </fieldset>
                        </RadioGroup>
                        </td>
                        <td>
                        <TextField
                            className={classes.tField}
                            id="itemCode"
                            InputProps={{
                                endAdornment: (
                                    <Button
                                        variant="contained"
                                        size="small"
                                        color="secondary"
                                        onClick={() => handleClickOpen()}
                                        value="itemCode"
                                    >
                                        검색
                                    </Button>
                                ),
                            }}
                            margin="normal"
                            placeholder="품목코드검색"
                            disabled={true}
                            value={getItemCode.value}
                        />
                    </td>
                    <td>
                        <RadioGroup
                            row
                            aria-label="condition"
                            onChange={onConditionChange}
                        >
                            <fieldset className={classes.text}>
                                <legend>
                                    <strong>BOM검색조건</strong>
                                </legend>
                                <FormControlLabel
                                    value="forward"
                                    control={<Radio />}
                                    label="정전개"
                                />
                                <FormControlLabel
                                    value="reverse"
                                    control={<Radio />}
                                    label="역전개"
                                />
                            </fieldset>
                            <Button
                            variant="contained"
                            color="secondary"
                            size="medium"
                            onClick={() => searchBom()}
                            value="searchBom"
                        >BOM조회</Button>
                        </RadioGroup>
                    </td>
                    </tr>
                <SearchItemCodeDialog data={data} open={openItemCodeDialog} close={handleClose} />
            </div>
        </>
    );
};

export default DeployHead;