import React from "react";
import { useSelector } from "react-redux";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";

const CashJournalGrid = () => {
  const data = useSelector(({ AccReducer }) => AccReducer.cashJournalList);

  const accountColumnDefs = [
    {
      headerName: "해당월",
      field: "monthReportingDate",
      width: 70,
      cellStyle: { textAlign: "center" },
    },
    {
      headerName: "일자",
      field: "reportingDate",
      width: 172,
      cellStyle: { textAlign: "center" },
    },
    {
      headerName: "적요",
      field: "expenseReport",
      width: 172,
      cellStyle: { textAlign: "center" },
    },
    {
      headerName: "거래처",
      field: "customerCode",
      width: 172,
      cellStyle: { textAlign: "center" },
    },
    {
      headerName: "거래처명",
      field: "customerName",
      width: 172,
      cellStyle: { textAlign: "center" },
    },
    {
      headerName: "입금",
      field: "deposit",
      width: 172,
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" 원"',
    },
    {
      headerName: "출금",
      field: "withdrawal",
      width: 172,
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" 원"',
    },
    {
      headerName: "잔액",
      field: "balance",
      width: 172,
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" 원"',
    },
  ];

  return (
    <>
      <div
        className={"ag-theme-balham"}
        style={{
          height: "70%",
          width: "100%",
          paddingTop: "20px",
        }}
      >
        <AgGridReact
          columnDefs={accountColumnDefs}
          rowData={data} // setData된 state를 결국 여기 넣어서 그리드에 표현함.
          rowSelection="single"
          onGidReady={e => {
            e.api.sizeColumnsToFit();
          }}
        />
      </div>
    </>
  );
};

export default CashJournalGrid;
